@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

:root {
  --main: #78ffdb;   /* MAIN */
  --blue: #8893af;    /* BLUE */
  --secondary: #0b1a2f; /* WHITE */
  --container-bg: #182b45;
  --hover-bg: #122a3b;
  /* --accent-color: #747474;    /* GRAY */
  --font-color: #ccd7f5;
  --font-light: rgb(136, 146, 176);
}

/* base styles */
html,body{
    background: linear-gradient(245.59deg, hsl(210, 32%, 44%) 0%, hsl(215, 33%, 33%) 28.53%, hsl(216, 50%, 15%) 75.52%);
  
    height: 100%;
    width: 100%;
}

* {
  margin: 0;
  font-family: "Quicksand";
  color: var(--font-color);
  margin-bottom: 0;
  padding-bottom: 0;
}

.navbar {
  /* position: sticky; */
  top: 0;
  z-index: 999;
  padding: 20px;
  display: flex;
  align-items: center;
  max-width: 60em;
  margin: 0 auto;
  border-bottom: 1px solid var(--font-light);
}

.links {
  display: flex;
  align-items: center; /* Vertically center items in the navbar */
  justify-content: center; /* Horizontally center navbar items */
  gap: 20px; /* Adjust the space between navbar items */
}

.centered-link {
  display: inline-flex; /* Keeps the link inline but applies flex to its content */
  align-items: center;
  text-decoration: none;
  color: inherit;
  gap: 8px;
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}

/* Targets the link itself on hover */
.centered-link:hover {
  color: var(--main); /* Changes the text color */
}

/* Targets the icon within the link on hover */
.centered-link:hover svg {
  fill: var(--main); /* Changes the icon color */
}


.navbar h1 {
}
.navbar .links {
  
  margin-left: auto;
}
.navbar a {
  
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}

.link-with-icon {
  display: flex;
  align-items: center;
  gap: 0.5em;
}
.navbar a:hover {
  
  color: var(--main);
  fill: var(--main);
}
.content {
  padding-bottom: 0;
  margin-bottom: 0;
  max-width: 60em;
  margin: auto auto;
  padding: 20px;
}



/* blog previews  this is for hovering the things on the blog list/ list */ 
.blog-preview {
  transition: transform 0.2s, background-color 0.2s;
  padding: 10px 16px;
  margin: 20px 0;
  border-left: 4px solid #747474;
  border-radius: 3px;
  box-shadow: 1px 3px 5px rgba(0,0,0,0.1);
  background-color: var(--container-bg); /* Add background color */
}
.blog-preview:hover {
  transform: scale(1.03);
  box-shadow: 2px 6px 10px rgba(0,0,0,0.1);
  background-color: var(--hover-bg);
  border-color: var(--main);
}
.blog-preview:hover * {
  background-color: var(--hover-bg);
}
.blog-preview *{
  transition: transform 0.2s, background-color 0.2s;
  background-color: var(--container-bg);
}




.blog-tag{
  color: var(--main);
  cursor: pointer;
  text-decoration: underline;

}

.blog-preview h2 {
  font-size: 20px;
  color: var(--font-color);
  margin-bottom: 8px;
}

.blog-preview:hover .LuGlasses {
  stroke: rgb(255, 255, 255);
  stroke-width: 1.5px;
}





.blog-preview a{
  text-decoration: none;
}


/* blog details page */
.blog-details h2 {
  font-size: 20px;
  color: var(--main);
  margin-bottom: 10px;
}
.blog-details div {
  margin: 20px 0;
}
.blog-details button {
  color: var(--main);
  border: 1px solid var(--main);
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}
.blog-details button:hover {
  background-color: var(--hover-bg);
}

/* create new blog form */
.create {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
  padding-top: 20px;
}
.create label {
  text-align: left;
  display: block;
}
.create h2 {
  font-size: 20px;
  color: var(--main);
  margin-bottom: 30px;
}
.create input, .create textarea, .create select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}


input:active, input:focus, textarea:active, textarea:focus, select:active, select:focus{
  border: 1px solid var(--main);
  outline: none;
  box-shadow: none;
  background-color: var(--hover-bg);
}
input, input, textarea, textarea, select, select{
  border: 1px solid var(--main);
  outline: none;
  box-shadow: none;
  background-color: rgba(0,0,0,0);
}

label:active, label:focus, label:hover{
  color: var(--main);
}

.blog-specs{
  display: flex;
  gap: 2em;
}
.blog-preview-container{
  display: flex;
  justify-content: space-between;
}

.LuGlasses{
  stroke: var(--font-color)
}
/* highlighting icon and readtime in blog list */

.display-linebreak {
  white-space: pre-line;
}

.smallText{
  font-size: 50;
  color:#747474;
}

.show-date {
